@import '../styles/settings';

.toolkit-prototype {
  .tutorial {
    height: 100%;
    overflow: unset;
    background-color: $ui-01;
  }

  .tutorial__header {
    display: flex;
    align-items: center;
    box-sizing: content-box;
    height: $layout-05;
    padding: $spacing-02 $spacing-04;
  }

  .tutorial__content {
    height: 100%;
    overflow: unset;
    background: $ui-background;

    .bx--accordion {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      overflow: unset;
    }
  }

  .tutorial__breadcrumb .bx--breadcrumb-item .bx--link {
    @include carbon--type-style('productive-heading-03');

    white-space: no-wrap;
  }

  .tutorial__breadcrumb .bx--breadcrumb-item {
    &:not(.bx--breadcrumb-item--current) .bx--link {
      @include carbon--type-style('productive-heading-03');
    }

    &.tutorial__breadcrumb--tutorials {
      width: 100%;
    }

    &.tutorial__breadcrumb--tutorials .bx--link {
      @include carbon--type-style('label-01');

      display: flex;
      align-items: center;
      margin-bottom: $spacing-02;
      fill: $interactive-01;
    }

    .tutorial__breadcrumb__left-arrow {
      margin-right: $spacing-02;
    }

    &::after {
      content: '';
    }
  }

  .tutorial-start {
    width: 100%;
    max-width: 100%;
    height: $layout-05;
  }

  .bx--accordion__heading {
    display: flex;
    align-items: center;
    padding: 0;
  }

  .bx--accordion__title {
    @include carbon--type-style('productive-heading-01');
  }

  .bx--btn.bx--btn--icon-only.bx--tooltip__trigger {
    max-height: 48px;
  }

  .tutorial-stage--preview__title {
    height: 100%;
    display: flex;
    min-height: 2.5rem;
    max-height: 2.5rem;
    align-items: center;
  }

  .bx--accordion__item--active {
    display: flex;
    flex-direction: column;
    overflow-x: visible;
    overflow-y: auto;
    height: 100%;
    padding-left: .5rem;
    padding-right: .5rem;
    background: $ui-background;
  }

  .list-panel-item__start-button {
    line-height: 1.59;
  }
}
