.ide--create-header {
  transition: $duration--moderate-02;
  padding-bottom: $spacing-05;
  &.fixed {
    position: fixed;
    z-index: 2;
    width: 100%;
    height: $layout-04;
    background-color: $ui-01;
    transition: $duration--moderate-02;
    box-shadow: inset 0px -1px 0px $ui-03;
    padding-bottom: 0;

    .ide--create-breadcrumb {
      display: none;
    }

    .ide--create-heading {
      display: inline-block;
    }

    .ide--create-heading-heading {
      @include carbon--type-style('body-short-01');
      transition: $duration--moderate-02;
      line-height: $layout-04;
    }

    .ide--create-form-subtitle {
      display: inline-block;
      @include carbon--type-style('body-short-01');
      color: $text-02;
      transition: $duration--moderate-02;
      line-height: $layout-04;
    }

    .ide--create-form-subtitle-content {
      margin-left: $spacing-03;
      padding-left: $spacing-03;
      border-left: 1px solid $text-02;
      animation: reveal $duration--moderate-02 $duration--moderate-01;
      animation-fill-mode: both;
    }

    .#{$prefix}--grid,
    .#{$prefix}--col {
      padding-right: 0;
      transition: $duration--moderate-02;
    }

    .#{$prefix}--row {
      margin-right: 0;
      transition: $duration--moderate-02;
    }

    .ide--create-buttons {
      :not(:only-child()) {
        margin-left: 0;
      }
    }
  }
}

@keyframes reveal {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.ide--create-title {
  align-content: flex-end;
  display: flex;
  height: auto;
}

.ide--create-title-box {
  flex: 1;
  align-self: flex-start;
}

.ide--create-heading-heading {
  transition: $duration--moderate-02;
  @include carbon--type-style('productive-heading-05');
}

.ide--create-form-subtitle {
  @include carbon--type-style('productive-heading-03');
}

.ide--create-breadcrumb {
  height: $layout-04;
  align-content: flex-end;
  margin-bottom: carbon--mini-units(1);
}

.ide--create-pre-check {
  display: none;
}

.ide--create-form {
  display: none;
}

.ide--create-show-pre-check .ide--create-pre-check {
  display: block;
}

.ide--create-show-form .ide--create-form {
  display: block;
}

.ide--create-buttons {
  :not(:only-child()) {
    margin-left: $spacing-03;
  }
}

@include carbon--breakpoint-down(672px) {
  .ide--create-buttons {
    position: fixed;
    z-index: 2;
    width: 100%;
    bottom: 0;
    right: 0;
    background-color: $ui-01;
    box-shadow: inset 0px 1px 0px $ui-03;
    :not(:only-child()) {
      margin-left: 0;
    }
  }

  .ide--create-content {
    margin-bottom: $layout-04;
  }
}

.ide--create-button-box {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.ide--create-cancel-btn {
  margin-right: carbon--mini-units(1);
  width: carbon--mini-units(30);
}

.ide--create-next-btn {
  display: flex;
  width: carbon--mini-units(30);
}
