@import '../styles/settings';

.toolkit-prototype {
  .list-panel-item__heading {
    display: flex;
    justify-content: flex-start;
    align-content: flex-end;
    margin-bottom: $spacing-02;
  }

  .list-panel-item__title {
    @include carbon--type-style('productive-heading-03');
    color: $interactive-01;
    margin-top: $spacing-02;
    margin-right: $spacing-04;
  }

  .list-panel-item__text {
    @include carbon--type-style('body-long-01');
  }

  .list-panel-item {
    .list-panel__button {
      transform: 0.6s all ease-in-out;
      opacity: 0;
      margin-top: -$spacing-08;
    }

    &--active .list-panel__button {
      opacity: 1;
      margin-top: 0;
    }
  }

  .list-panel__buttons {
    padding-left: $spacing-05;
  }

  .list-panel-item__lower {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: .5rem;

    color: $text-01;
    fill: $text-01;

    .list-panel-item__duration-img,
    .list-panel-item__duration-text {
      margin-right: $spacing-02;
    }
  }

  .list-panel-item__lower-left {
    display: flex;
    align-items: center;
  }

  .list-panel-item__arrow-right {
    fill: #6f6f6f;
    opacity: 0.8;
    transition: 0.3s all ease-in-out;
  }
  .list-panel-item .bx--tile:focus,
  .list-panel-item .bx--tile:hover {
    .list-panel-item__arrow-right {
      opacity: 1;
    }
  }
}
