.ide--import-container {
  .bx--file__drop-container {
    max-width: 100%;
  }
  .ide--file-list {
    margin-top: carbon--mini-units(10);
    .ide--file-list-status {
      @include carbon--type-style('body-short-01');
      margin-bottom: $spacing-03;
    }
    .bx--file__selected-file {
      max-width: 100%;
    }
  }
  .ide-file-by-url {
    margin-top: $spacing-07;
  }
  .ide-importing-description {
    @include carbon--type-style('body-short-01');
    margin-bottom: $spacing-06;
    margin-top: $spacing-03;
  }
  // the bold headers
  h5 {
    margin-bottom: $spacing-03;
  }
  .ide-importing-drop-description {
    margin-bottom: $spacing-05;
    @include carbon--type-style('body-short-01');
    color: $text-01;
  }
  .bx--file-browse-btn {
    margin-bottom: none;
  }

  .ide-importing-flex-input {
    display: flex;
    width: 100%;
    .bx--form-item {
      flex: 1 0 8rem;
      margin-right: $spacing-05;
    }
    .ide-importing-flex-button {
      flex: 0 0 auto;
    }
  }
}
