$ide-card-size: carbon--mini-units(30);

.ide--card {
  background-color: $ui-background;
  position: relative;
  flex: 1 0 21%;
  display: flex;
  margin: carbon--mini-units(0.2);
  padding: 0;
  min-height: $ide-card-size;
  height: calc(100% - #{carbon--mini-units(0.4)}); // Double the margin above

  .ide--card--card-overides {
    flex-grow: 1; // let the card consume the entire space in the container
    margin: 0;
    padding: carbon--mini-units(2);
    position: relative;
  }
}

.ide--card-container {
  min-width: $ide-card-size;
}

.ide--card-container__large {
  min-width: $ide-card-size * 2;
}

.ide--card--card-overides,
.ide--card--card-overides:hover,
.ide--card--card-overides:visited,
.ide--card--card-overides:visited:hover {
  // carbon overides to remove blue text color
  color: inherit; // no blue link color
  text-decoration: none; // no underline
}

.ide--card--tile {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  height: 100%; // use all height in the container
}

.ide--card--tile-img-group {
  display: flex;
  justify-content: space-between;
}

.ide--card--tile-img-group-right {
  direction: rtl;
}

.ide--card--tile-img {
  height: carbon--mini-units(6);
  width: carbon--mini-units(6);
  margin-bottom: carbon--mini-units(2);
  display: flex;
  align-items: center;
  justify-content: center;
}

.ide--card--text-container {
  flex: 1;
  display: flex;
  flex-direction: column;

  .ide--card---tile-text {
    @include carbon--type-style('body-short-01');
  }

  .ide--card-heading-container {
    flex-grow: 1;
    .ide--card--heading {
      @include carbon--type-style('productive-heading-03');
      margin-bottom: $spacing-02;
    }
  }

  .ide--card--heading-title,
  .ide--card--heading-description {
    font-size: carbon--mini-units(1.5);
  }
}

.ide--card--card-disabled {
  background-color: $card__disabled-background-colour;

  .ide--card--card-overides {
    background-color: $card__disabled-background-colour;
    cursor: not-allowed;

    &:hover {
      background-color: $card__disabled-background-colour; // force background color change on hover if the card is disabled
    }
  }

  .ide--card--tile-img,
  .ide--card--heading-title,
  .ide--card--heading-description {
    opacity: 0.4;
  }

  .#{$prefix}--tooltip__label {
    visibility: hidden; // needed to disable the carbon tooltip icon
  }

  .ide--card--card-tooltip {
    width: carbon--mini-units(2);
    padding-left: carbon--mini-units(12);

    /* As we're faking the toolip over the whole card, the caret points at the wrong location. Better to hide it*/
    margin-left: -0.5rem; // Account for the caret
    .bx--tooltip__caret {
      display: none;
    }
  }
}
