@import '../styles/settings';

.toolkit-prototype {
  .tutorials {
    padding-left: $spacing-06;
  }

  .IdePageHeader {
    padding-left: $spacing-06;
    padding-top: $spacing-06;
  }

  .tutorials__serach {
    margin-bottom: $spacing-06;
  }

  .tutorials-page__content {
    padding-top: $spacing-03;
    padding-left: $spacing-03;
  }

  .tutorials-page__section {
    margin-top: $spacing-09;
    margin-left: calc(-1 * #{$layout-01});
  }

  .tutorials-page__sub-heading {
    @include carbon--type-style('productive-heading-04');

    margin: $spacing-07 $spacing-05 $spacing-03;
  }

  .tutorials-page__sub-heading--short {
    margin-bottom: 0;
  }

  .promoted-tutorial__container {
    min-width: 17rem;
    margin-bottom: $spacing-08;
    padding: 0 $spacing-04;
  }

  .promoted-tutorial__tile {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
  }

  .promoted-tutorial__lower {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .promoted-tutorial__lower-left {
    display: flex;
    align-items: center;
  }

  .promoted-tutorial__img {
    width: 180px;
    padding: $spacing-05 $spacing-03;
  }

  .promoted-tutorial__text {
    @include carbon--type-style('heading-02');
    font-weight: 400;
  }

  .promoted-tutorial__arrow-right {
    opacity: 0.8;
    transition: 0.3s all ease-in-out;
  }
  .promoted-tutorial__tile:focus,
  .promoted-tutorial__tile:hover {
    .promoted-tutorial__arrow-right {
      opacity: 1;
    }
  }

  .tutorials__promoted-list {
    display: flex;
    margin-top: $spacing-05;

    .ide--card-container {
      margin-bottom: $spacing-09;
    }
  }

  .promoted-tutorial__button {
    position: absolute;
    max-width: initial;
    width: 50%;
    top: 100%;
    left: 0;

    transform: 0.6s all ease-in-out;
    opacity: 0;
    margin-top: -$spacing-08;

    &--active {
      opacity: 1;
      margin-top: 0;
    }
    &:last-child {
      left: 50%;
    }
  }

  .ide-slide-over-panel--contents {
    > header {
      box-shadow: none;
      height: auto;
      background-color: var(--cds-ui-background, #ffffff);
      min-height: 4rem;

      svg {
        fill: $ui-01;
      }

      h3 {
        padding: .5rem .75rem;
        font-size: 1.25rem;
        line-height: 2rem;
        align-self: center;
      }
    }

    .ide-slide-over-panel--children {
      padding: 0;
      overflow: unset;
      overflow-y: auto;
      overflow-x: visible;
    }
  }

  .promoted-tutorial__duration {
    @include carbon--type-style('productive-heading-02');
    @include carbon--font-weight('regular');

    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    color: $text-05;
    fill: $text-05;

    .promoted-tutorial__duration-text {
      margin-left: $spacing-02;
    }
  }

  .ide-filter .ide-filter--tag {
    &>span {
      display: flex;
      align-items: center;
    }
  }

  .bx--breadcrumb .bx--link {
    white-space: unset;
  }

  .tutorial__breadcrumb {
    padding-right: 1rem;
  }

  .tutorials__list {
    .list-panel-item {
      background: $ui-01;
      margin-left: 12px;
      max-width: 100%;
      margin-bottom: .5rem;

      .bx--tile {
        background: $ui-01;

        &:hover {
          background: #e5e5e5;
        }
      }
    }
  }
}
