@import '../styles/settings';

.toolkit-prototype {
  .IdeNavigation--content {
    margin-left: 3rem;
    padding: 2rem;
  }

  .layout-grid {
    display: grid;
    grid-gap: $spacing-02;
    height: 100%;
    grid-template-areas:
      'menu menu menu'
      'toolbar toolbar toolbar'
      'left main tutorial'
      'bottom-left bottom-right tutorial';
    grid-template-rows: 40px 32px 7fr 3fr;
    grid-template-columns: 300px auto 380px;

    background-color: $ui-03;

    &.layout-grid--main-only {
      grid-template-areas:
        'menu menu menu'
        'toolbar toolbar toolbar'
        'main main main'
        'main main main';
    }
  }

  .layout-grid__main,
  .layout-grid__left,
  .layout-grid__bottom,
  .layout-grid__tutorial {
    min-height: 0;
    min-width: 0;
  }

  .layout-grid__menu {
    grid-area: menu;
  }

  .layout-grid__toolbar {
    grid-area: toolbar;
  }

  .layout-grid__left {
    grid-area: left;
  }

  .layout-grid__main {
    position: relative;
    grid-area: main;

    .bx--header {
      position: absolute;
    }

    .bx--side-nav {
      position: absolute;
    }
  }

  .layout-grid__tutorial {
    grid-area: tutorial;
    height: calc(100vh - 72px - #{$spacing-03});
  }

  .layout-grid__bottom-left {
    grid-area: bottom-left;
  }

  .layout-grid__bottom-right {
    grid-area: bottom-right;
  }
}
