.IdePageHeader__backgroundColour,
.IdePageHeader {
  background-color: $ui-02;
}

.IdePageHeader {
  box-shadow: inset 0px -1px $ui-01;
}

.IdePageHeader--breadcrumb-container {
  min-height: carbon--mini-units(5);
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: center;
}

.IdePageHeader--heading-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}

.IdePageHeader--subtitle-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}

.IdePageHeader--heading {
  @include carbon--type-style('expressive-heading-05');
}

.IdePageHeader--subtitle {
  @include carbon--type-style('expressive-heading-02');
  color: $text-05;
  font-weight: 400;
}

.IdePageHeader--tabs-container {
  margin-left: carbon--mini-units(-2);
  min-height: carbon--mini-units(6);
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: flex-end;
}

.IdePageHeader--tabs-container__empty {
  min-height: carbon--mini-units(4);
}

.IdePageHeader--custom-content-container {
  padding-right: 0;
}
