@import '../styles/settings';

.toolkit-prototype {
  .bx--header {
    display: flex;
    justify-content: space-between;
  }

  .bx--side-nav--expanded,
  .bx--side-nav.bx--side-nav--rail:not(.bx--side-nav--fixed):hover {
    width: $spacing-09 !important;
  }

  .bx--side-nav .bx--side-nav__toggle {
    display: none;
  }

  .bx--header__nav::before {
    display: none;
  }

  .header-highlight {
    font-weight: bold;
  }

  .main-content {
    height: auto;
    max-width: 55rem;
  }
}
