@import '../../globals/scss/_colors.scss';
$ide-home-header-height: 25rem;
$ide-home-header-collapsed-height: 3rem;
$ide-home-card-height: 16rem;

.ide--home {
  height: 100%; // use all height
  background-color: $ui-01; // use inverse background colour
}

.ide--home-header {
  height: $ide-home-header-height;
  transition-property: height;
  transition-duration: 0.5s;
  padding: 0 carbon--mini-units(3);
  @include carbon--motion(standard, expressive);

  &.collapsed {
    height: $ide-home-header-collapsed-height;
  }
}

.ide--home-heading-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  height: 100%; // use all height in the container
}

.ide--home-image-container {
  margin: auto;
}

.ide--home-table .#{$prefix}--row {
  margin-left: 0px;
  margin-right: 0px;
}

.ide--home-image {
  width: 100%;
}

.ide--home-product-heading {
  @include carbon--type-style('productive-heading-03');
  margin-bottom: $spacing-02;
}

.ide--home-call-to-action {
  @include carbon--type-style('productive-heading-05');
}

// this class sets up height for all cards, and disabled carbon width and color changes
.ide--home-tile-layout,
.ide--home-tile-layout:hover {
  min-height: $ide-home-card-height;
  height: $ide-home-card-height;
  min-width: unset; // no min width
  color: inherit; // no blue link color
  text-decoration: none; // no underline
}

.ide--home-grid-gutter {
  margin-bottom: $spacing-01;
  flex: 1 0 50%;
  max-width: 50%;
  @media (min-width: 42rem) {
    flex: 1 0 25%;
    max-width: 25%;
  }
}

.ide--home-full-width,
.ide--home-tab-content {
  width: 100%;
  padding-top: carbon--mini-units(2);
}

.ide--home-tab-content {
  padding: 0;
}

.ide--home-tab-content[hidden] {
  display: none;
}

.ide--home-tile {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  height: 100%; // use all height in the container
}

.ide--home-tile-img {
  height: 2rem;
  width: 2rem;
}

.ide--home-tile-heading {
  @include carbon--type-style('productive-heading-03');
  margin-bottom: $spacing-02;
}

.ide--home-tile-subtext {
  @include carbon--type-style('body-short-01');
}

.ide--home-header-toggle-container {
  white-space: nowrap;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
}
