.ide-slide-over-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: z('hidden');
  transition: background-color $duration--slow-02 motion(exit, expressive),
    opacity $duration--moderate-02 motion(exit, expressive),
    z-index $duration--slow-02 motion(exit, expressive),
    visibility $duration--moderate-02 motion(exit, expressive);

  &.ide-slide-over-overlay--open {
    z-index: z('modal');
    visibility: visible;
    opacity: 1;
    background-color: $overlay-01;
  }
}

.ide-slide-over-panel {
  position: fixed;
  z-index: 10000;
  display: flex;
  flex-direction: column;
  background-color: $ui-background;
  top: 0;
  bottom: 0;
  right: 0;
  box-shadow: -2px 0px 4px 0px rgba(0, 0, 0, 0.25);
  transform: translateX(calc(100% + 6px));
  transition: 0.3s ease-out;

  &.ide-slide-over-panel--small {
    width: 20rem;
  }

  &.ide-slide-over-panel--medium {
    width: 30rem;
  }

  &.ide-slide-over-panel--large {
    width: 40rem;
  }

  &.ide-slide-over-panel--open {
    transform: translateX(0);
  }

  .ide-slide-over-panel--contents {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    flex: 1;
    display: flex;
    flex-direction: column;
    header {
      box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.1);
      display: flex;
      h3 {
        flex: 1;
        line-height: 3rem;
        padding: 0 1rem;
        font-size: 1.125rem;
      }
    }
    .ide-slide-over-panel--footer {
      &-content {
        display: flex;
        margin-top: auto;
        height: $carbon--layout-05;

        button {
          max-width: none;
          flex: 1;
          height: $carbon--layout-05;
          margin: 0;
          padding-top: $carbon--layout-01;
          padding-bottom: $carbon--layout-03;
        }
      }
      display: flex;
      & > div {
        width: 100%;
      }
      button {
        flex: 1;
      }
    }
    .ide-slide-over-panel--close {
      &::before {
        display: none;
      }
      .#{$prefix}--assistive-text {
        display: none;
      }
    }

    .ide-slide-over-panel--children {
      padding: 1rem;
      overflow: auto;
      flex: 1;
    }
  }
}
