.ide--http-errors {
  height: 100%;
  min-width: $sm;
  max-width: $max;
  width: 100%;
}

.ide--http-errors-title {
  margin-top: $layout-04;
  @include carbon--type-style('productive-heading-02');
  @media (min-width: $md) {
    margin-top: $layout-07;
  }
}

.ide--http-errors-description {
  margin-top: $spacing-03;
  @include carbon--type-style('productive-heading-05');
  // sm
  @media (min-width: 0) and (max-width: $sm) {
    @include carbon--type-style('productive-heading-03');
  }
  // md
  @media (min-width: $sm + 1px) and (max-width: $md) {
    @include carbon--type-style('productive-heading-04');
  }
}

.ide--http-errors-details {
  margin-top: $spacing-05;
  @include carbon--type-style('body-short-01');
}

.ide--http-errors-links {
  margin-top: $spacing-06;
}
.ide--http-errors-links--link {
  margin-bottom: $spacing-03;
  @include carbon--type-style('body-short-01');
}

.ide--http-errors-image-container {
  position: fixed;
  z-index: -1;
}
.ide--http-errors-image {
  transform: translate(-35%, $layout-06);
  width: mini-units(175);
  @media (min-width: $max) {
    left: mini-units(17);
    position: fixed;
    top: $layout-05;
    transform: none;
  }
}
.ide--http-errors-inline-image {
  width: mini-units(125);
  z-index: -1;
  // sm
  @media (min-width: 0) and (max-width: $sm) {
    margin-left: mini-units(-16);
    margin-top: -$spacing-03;
    padding-top: $layout-05;
    @include carbon--type-style('body-short-01');
  }
  // md
  @media (min-width: $sm + 1px) and (max-width: $md) {
    margin-left: mini-units(-12);
    margin-top: -$spacing-03;
    padding-top: $layout-05;
  }
}
