@import './styles/settings';

.toolkit-prototype {
  color: $text-01;
  background-color: $ui-background;

  &.App {
    height: 100vh;

    .bx--side-nav--expanded,
    .bx--side-nav.bx--side-nav--rail:not(.bx--side-nav--fixed):hover {
      width: 16rem !important;
    }
  }

  .IdeNavigation--content {
    background: $ui-background;
    height: 100%;
  }

  .IdeNavigation--content,
  .ide--home,
  .ide--card {
    background: $ui-background;
    background-color: $ui-background;
  }

  .ide-slide-over-panel {
    visibility: hidden;
    width: 0;
  }

  .ide-slide-over-panel--open {
    visibility: visible;
    display: block;
    width: 30rem;
  }

  .IdeNavigation--content {
    z-index: 2;
    background: $ui-background;
    padding-bottom: 3rem;
    height: 100%;
  }

  :not(.bx--side-nav--expanded) >
  .bx--side-nav__items >
  .bx--side-nav__item >
  .IdeNavigation--altHover-link:hover .bx--side-nav__link-text {
    position: inherit;
    display: flex;
    align-items: center;
    background-color: inherit;
    margin-left: 0;
    padding: .5rem 1rem .5rem 0;
    border-radius: 0;
    font-weight: inherit;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 0.875rem;
    line-height: 1.25rem;
    letter-spacing: 0.1px;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color: $ui-01 !important;
  }

  :not(.bx--side-nav--expanded) >
  .bx--side-nav__items >
  .bx--side-nav__item >
  .IdeNavigation--altHover-link:hover .bx--side-nav__link-text::before {
    content: unset;
    visibility: hidden;
  }

  .IdeNavigation--nav-theme a.bx--side-nav__link.active {
    background-color: $interactive-02;
    color: $active-ui;
  }

  .IdeNavigation--nav-theme .bx--side-nav__submenu:hover,
  .IdeNavigation--nav-theme a.bx--side-nav__link:hover {
    background-color: $interactive-02 !important;
    color: $active-ui !important;
  }

  .bx--accordion__item--active .bx--accordion__content {
    padding-right: 1rem;
  }

  :not(.bx--side-nav--expanded) >
  .bx--side-nav__items >
  .bx--side-nav__item >
  .IdeNavigation--altHover-link:hover .bx--side-nav__link-text::after {
    position: fixed;
    width: 0;
    height: 0;
    margin-left: 0;
    content: unset;
    visibility: hidden;
  }

  .IdePageHeader--breadcrumb-container {
    display: none;
  }

  .bx--header__nav {
    display: block;
  }

  .IdeNavigation {
    position: relative;
    padding-top: 0;
    height: calc(100% - 3rem);
  }

  .bx--header {
    z-index: 1;
    position: relative;
  }
}
