.IdeEmptyState {
  display: flex;
  flex-direction: row;
}
.IdeEmptyState--format-sm {
  flex-direction: column;
}

.IdeEmptyState__image-container {
  align-self: flex-start;
  margin-right: $spacing-08;
}
.IdeEmptyState__image-container--format-sm {
  margin-right: 0;
}
.IdeEmptyState__image {
  display: block;
  height: carbon--mini-units(19);
  width: carbon--mini-units(15);
}
.IdeEmptyState__image--format-sm {
  height: carbon--mini-units(11);
  width: carbon--mini-units(11);
}

.IdeEmptyState__content-container {
  align-self: center;
}
.IdeEmptyState__content-container--format-sm {
  align-self: start;
}
.IdeEmptyState__title {
  @include carbon--type-style('productive-heading-02');
  padding-top: 0;
}
.IdeEmptyState__title--format-sm {
  padding-top: $spacing-05;
}
.IdeEmptyState__body {
  @include carbon--type-style('caption-01');
  color: $text-02;
  margin-top: $spacing-03;
}
.IdeEmptyState__button {
  margin-top: $spacing-04;
}
.IdeEmptyState__link {
  @include carbon--type-style('body-short-01');
  margin-top: $spacing-03;
}
.IdeEmptyState__link:first-of-type {
  margin-top: $spacing-05;
}
