@import '../styles/settings';

.toolkit-prototype {
  .tutorial-page__tutorial {
    min-height: 100%;
    height: auto;
    padding-bottom: 1rem;
    background-color: var(--cds-ui-background, #ffffff);
  }

  .tutorials-page__tutorial--hr {
    margin: 0;
  }

  .tutorials-page__tutorial--title-container {
    padding: .75rem 2rem;
    display: flex;
    flex-direction: column;
  }

  .tutorials-page__tutorial--title {
    font-size: 1.25rem;
    line-height: 2rem;
  }

  .tutorials-page__tutorial--title-link {
    align-self: flex-end;
    margin-top: .5rem;
  }

  .tutorials-page__tutorial--content-container {
    padding: .5rem 1rem;
    height: 100%;
  }

  .tutorials-page__tutorial--subtitle-container {
    padding: 0 .5rem;
  }

  .tutorials-page__tutorial--subtitle {
    padding: .5rem;
  }

  .tutorials-page__tutorial--subtitle-link{
    margin: .5rem;
  }

  .bx--accordion__item--active {
    padding-left: 0;
    padding-right: 0;
  }

  .tutorial-preview__section {
    .tutorial-preview__section-header {
      font-weight: 600;
      margin: .5rem 0;
      text-transform: inherit;
    }
  }

  .uicontrol,
  .application {
    font-weight: 600;
  }

  .tutorial-preview__section-inline {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .tutorial-preview__text {
    max-width: 100%;
    text-transform: none;
  }

  .bx--list--ordered {
    text-transform: none;
  }

  .tutorial-preview__section--ordered-list {
    margin-left: 2rem;
  }

  .tutorial-preview__section--list {
    padding-bottom: .5rem;
  }

  code {
    background-color: #f4f4f4;
  }

  b {
    font-weight: 600;
  }

  img {
    width: auto;
    max-width: 100%;
    height: auto;
    vertical-align: top;
  }

  .bx--accordion__item {
    display: flex;
  }

  .bx--accordion__item:last-child {
    margin-top: 0;
  }

  .bx--accordion__heading {
    flex: unset;
    border: 0;
    border-left: 2px solid transparent;
    border-right: 2px solid transparent;
  }

  .bx--accordion__heading:hover::before,
  .bx--accordion__heading:focus::before,
  .bx--accordion__heading:active::before {
    top: 0;
    border: 0;
    height: 100%;
  }

  .bx--accordion__heading:focus,
  .bx--accordion__heading:active, {
    border: 2px solid #0f62fe;
  }

  .tutorial-preview__section-button {
    padding: calc(0.375rem - 2px) 60px calc(0.375rem - 3px) 12px;

    .bx--btn__icon {
      top: 6px;
    }
  }

  .tutorial-preview__section--list-substep {
    list-style-type: circle;
    margin-left: 1rem;
  }
}
