.ide-filter {
  position: relative;
  .highlight {
    background-color: $blue-20;
  }
  .ide-filter--search-icon {
    position: absolute;
    top: calc(50% - 8px);
    z-index: 1;
    left: 12px;
    opacity: 0.75;
  }

  .ide-filter--tag-filter {
    &:hover {
      background: $inverse-hover-ui;
      border-radius: 50%;
    }
    &:focus {
      box-shadow: inset 0 0 0 2px $inverse-focus-ui;
      border-radius: 50%;
      outline: none;
    }
  }

  .ide-filter--close {
    cursor: pointer;
    color: inherit;
    fill: currentColor;
    width: 1.25rem;
    height: 1.25rem;
    background: none;
    border: none;
    padding: 0.125rem;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      background: rgba(0, 0, 0, 0.15);
      border-radius: 50%;
    }
    > svg {
      height: 8px;
      width: 8px;
      vertical-align: middle;
    }
  }
  .ide-filter--clear {
    cursor: pointer;
    height: 3rem;
    width: 3rem;
    fill: $text-01;
    &:hover {
      background: $hover-field;
    }
    > svg {
      margin: 19px;
    }
  }
  .ide-filter--tag {
    font-size: 1rem;
    margin-left: 0;
    .ide-filter--category {
      font-weight: bold;
      margin-right: 0.25rem;
    }
  }
}

/** React select prefixes classes
https://react-select.com/styles#using-classnames
Can't do in js as doesn't play well with carbon themes

importants because css ordering on component doesnt work otherrwise
**/
.ide-filter__control {
  border-bottom-color: $ui-04 !important;
  border-bottom-style: solid !important;
  border-bottom-width: 1px !important;
  background-color: $field-01 !important;

  &:focus,
  &:hover {
    outline: 2px solid $focus;
  }
}

.ide-filter__menu {
  background-color: $field-01 !important;
}

.ide-filter__placeholder {
  color: $ui-04 !important;
}

.ide-filter--light {
  .ide-filter__control {
    background-color: $field-02 !important;
  }
}
