@import '../styles/settings';

.tutorial-preview,
.tutorial-preview.bx--accordion__item--active {
  height: 100%;
  max-height: calc(100% - #{$layout-05});
}

.tutorial-preview {
  .tutorial-preview__text-header {
    @include carbon--type-style('body-long-01');
    margin-bottom: $spacing-02;
  }

  .tutorial-preview__text {
    @include carbon--type-style('body-long-01');
    margin-bottom: $spacing-02;
  }

  .tutorial-preview__section {
    margin-bottom: $spacing-02;
    text-transform: inherit;

    & > *:first-child {
      @include carbon--type-style('productive-heading-01');
      margin: $spacing-05 0 $spacing-03;
      text-transform: inherit;
    }

    .tutorial-preview__schedule {
      @include carbon--type-style('label-01');

      display: flex;
      align-items: center;
      margin-top: calc(-1 * #{$spacing-02});
      margin-bottom: calc(-1 * #{$spacing-04});
      color: $text-05;
      fill: $text-05;
      min-height: unset;
      height: 32px;
    }
  }

  .tutorial-preview__list {
    margin-bottom: $spacing-02;
    padding-left: $layout-01;
  }

  .tutorial-preview__icon {
    width: $spacing-05;
    height: $spacing-05;
    margin: $spacing-02;
  }

  .bx--btn.bx--btn--icon-only.bx--tooltip__trigger {
    max-height: 48px;
    align-self: baseline;
  }

  .tutorial-preview__downloads {
    display: flex;
    height: 100%;
    align-items: flex-end;
  }

  .tutorial-preview__section--row {
    @include carbon--type-style('label-01');

    display: flex;
    align-items: baseline;
    justify-content: space-between;
    color: $text-05;
    fill: $text-05;
  }

  .tutorial-preview__schedule--container {
    height: 32px;
    align-items: center;
  }
}
