.ide--api-key-generation .bx--modal-content {
  @include carbon--breakpoint-down('lg') {
    width: 100%; // Use the full width of the modal viewport is less than lg (< 1056px)
    padding-right: $spacing-05;
  }
}

.ide--api-key-generation-description,
.ide--api-key-generation-descriptive-name,
.ide--api-key-generation-api-key,
.ide--api-key-generation-api-key-secret {
  margin-bottom: $spacing-05;
}

.ide--api-key-generation-descriptive-name--label {
  @include carbon--type-style('label-01');
  margin-bottom: $spacing-03;
}

.ide--api-key-generation-helper-text-container {
  display: flex;
}

.ide--api-key-generation-helper-text-icon {
  min-width: $spacing-05;
  margin-right: $spacing-03;
}
.ide--api-key-generation-helper-text {
  @include carbon--type-style('helper-text-01');
}

.ide--api-key-generation-download-link {
  @include carbon--type-style('label-01');
  font-style: normal;
  margin-left: $spacing-02;
}
